body {
  background-color: #EEE8D6;
}

h1, h2, h3, h4, h5, h6 {
  color: #548C27;
  font-family: "Oswald", sans-serif;
  margin: 0 10px;
}

p {
  font-family: "Oswald", sans-serif;
  margin: 0 10px;
}

.btn-default, .btn-hot, .btn-cool {
  display: inline-block;
  padding: 6px 12px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  font-family: "Oswald", sans-serif;
  user-select: none;
  color: #EEE8D6;
}

.btn-default {
  background: #022933;
}

.btn-hot {
  background: #D14348;
}

.btn-cool {
  background: #0076A3;
}

.team {
  margin: 0;
  padding: 0;
  list-style: none;
}

.team img {
  width: 140px;
  float: left;
  margin-right: 10px;
}

.team .item {
  padding-bottom: 10px;
  padding-top: 10px;
  padding-left: 10px;
  border-top: 1px dotted #D14348;
}

.team .item:hover {
  background: #FFBA00;
}

.team .item:last-of-type {
  border-bottom: 1px dotted #D14348;
}

.team .item:before, .team .item:after {
  content: '';
  display: table;
}

.team .item:after {
  clear: both;
}

.team .head {
  margin: 0;
  padding: 0;
  color: #0076A3;
  font-size: 2rem;
}

#introduction .team .head {
  color: #D14348;
}

.team p {
  margin: 0;
}

.team a {
  text-decoration: none;
  color: #022933;
}

.background {
  color: #EEE8D6;
  margin: 20px 0;
  width: 100%;
  display: table;
}

.background .container {
  display: table-cell;
  vertical-align: bottom;
  padding-bottom: 20px;
}

.background .container h1 {
  color: #FFBA00;
  font-family: "Oswald", sans-serif;
  line-height: 105%;
  margin-bottom: 0;
  padding: 0 10%;
}

.background .container p {
  padding: 0 10%;
  margin-top: 0;
}

.jumbotron {
  background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.6)), url("../images/boats.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
}

.mesa {
  background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.6)), url("../images/skyisle.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
}
